import React from "react";
import { Col, Row, ProgressBar, OverlayTrigger } from "react-bootstrap";
import { Link45deg, PatchCheck } from "react-bootstrap-icons";
import { CourseTooltip } from "..";
import { color } from "../../utils";

interface CourseProgressProps {
  Title?: string;
  Progress?: number;
  Link?: string;
  Cert?: string;
  Finished?: boolean;
}

const CourseProgress = ({
  Title,
  Progress,
  Link,
  Cert,
}: CourseProgressProps) => {
  return (
    <div>
      <Row>
        <Col sm={12} md={6} style={{ paddingBottom: "50px" }}>
          {Title && <h2>{Title}</h2>}
          {Progress && (
            <ProgressBar>
              <ProgressBar
                now={Progress}
                key={1}
                style={{ fontSize: "15px" }}
                label={Progress >= 10 ? Progress + "%" : ""}
                aria-label="Progressbar"
              />
              {Progress !== 100 && (
                <ProgressBar
                  variant="danger"
                  now={100 - Progress}
                  key={2}
                  aria-label="Progressbar"
                />
              )}
            </ProgressBar>
          )}
        </Col>
        <Col sm={12} md={6} style={{ paddingBottom: "50px" }}>
          {Link && (
            <a href={Link} target="_blank" rel="noreferrer">
              <OverlayTrigger overlay={CourseTooltip}>
                <Link45deg
                  size={60}
                  style={{ marginRight: "15px", color: color.main }}
                  aria-label="Link to another course page"
                />
              </OverlayTrigger>
            </a>
          )}
          {Cert && (
            <a href={Cert} target="_blank" rel="noreferrer">
              <OverlayTrigger overlay={CourseTooltip}>
                <PatchCheck
                  size={60}
                  style={{ marginRight: "15px", color: color.main }}
                  aria-label="Link to certificate page"
                />
              </OverlayTrigger>
            </a>
          )}
        </Col>
      </Row>
    </div>
  );
};

export default CourseProgress;
